function ClubButtons(props) { // rock, paper, and scissor buttons
	if (props.value.includes("Basketball")) {
    return (
      <button class="btn btn-outline-primary mx-5" data-toggle="tooltip" data-placement="top" title="Basketball" onClick={props.onClick}>
        {<img src = 'https://i.pinimg.com/originals/9e/fd/1f/9efd1ff6e1ff0a004045780c3f9bec1f.jpg' width="200" height="200"/>}
      </button>
    );
  } else if (props.value.includes("Fitness Training")) {
     return (
      <button class="btn btn-outline-primary ml-5" data-toggle="tooltip" data-placement="top" title="Fitness Training" onClick={props.onClick}>
        {<img src = 'https://lh3.googleusercontent.com/proxy/FzgYyhByNA5YklT5HiqlPnpgxU4coerTtkkMnvrJCQh-8dFqVBrQzpy159hdzzpyYos4549az6lqqP_V6NPso1QdNzmxmiI' width="200" height="200"/>}
      </button>
     );
  } else if (props.value.includes("Running Track")) {
    return (
      <button class="btn btn-outline-primary mr-5" data-toggle="tooltip" data-placement="top" title="Running Track" onClick={props.onClick}>
        {<img src = 'https://static6.depositphotos.com/1157310/657/v/600/depositphotos_6579463-stock-illustration-running-race-on-track.jpg' width="200" height="200"/>}
      </button>
    );
  }
}

export default ClubButtons;